@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,400;0,700;1,400;1,700&display=swap);
/* Countdown Clock
/* ------------------------------------------------------------ */
.container-countdown 									{font-family: 'Roboto Condensed', sans-serif; margin: 0 auto; display: block; width: 320px;}

.container-countdown .title								{font-size: 21px; font-weight: bold; text-transform: uppercase; color: #f26622; position: relative; overflow: hidden; margin-bottom: 8px; text-align: center;}
.container-countdown .title span                       	{display: inline-block; position: relative;} 
.container-countdown .title span:before,
.container-countdown .title span:after                  {content: ""; position: absolute; height: 14px; border-bottom: 1px solid #f26622; top: -4px; width: 600px;}
.container-countdown .title span:before                 {right: 100%; margin-right: 4px;}
.container-countdown .title span:after                  {left: 100%; margin-left: 4px;}

.container-countdown .countdown_digit					{display: flex; flex-direction: row; text-align: center; flex-wrap: nowrap; justify-content: space-evenly;}
.container-countdown .digits,
.container-countdown .desc								{display: block; font-weight: bold; color: #044f7a;}
.container-countdown .digits							{font-size: 42px;}
.container-countdown .desc								{font-size: 17px; text-transform: uppercase;}

.container-countdown .divider							{font-size: 42px; font-weight: bold; color: #044f7a;}

/* Thermometer
/* ------------------------------------------------------------ */
.container-thermometer									{margin: 0 auto; display: block; width: 700px;}
.container-thermometer .container                       {position: relative; overflow: hidden;}
.container-thermometer .container, 
.container-thermometer .bg                              {height: 30px;}
.container-thermometer .meter,
.container-thermometer .current                         {height: 29px;}                        
.container-thermometer .current                         {padding-top: 2px; line-height: 26px;}
.container-thermometer .meter               
.container-thermometer .bg, 
.container-thermometer .meter                           {position: absolute; left: 0px; top: 0px;}
.container-thermometer .current                         {position: absolute; right: 0px;}
.container-thermometer .container                       {border: 1px solid lightgreen;}
.container-thermometer .bg                              {background:lightgreen; width: 100%;}
.container-thermometer .meter                           {background:lightcoral; width: 0%; transition: width 3.5s;}

.container-thermometer .current                         {text-align: right; padding-right: 5px; color:rgb(57, 112, 167); font-weight: bold;}

.container-thermometer .goal                            {padding-top: 5px; text-align: right; color:lightcoral; font-weight: bold;}

/* Countdown Clock
/* ------------------------------------------------------------ */
.container-slider 									    {margin: 0 auto; display: block; width: 768px; max-width: 90%}

.container-slider .menu                                 {margin: 0 0 30px; display: flex; flex-direction: row; flex-wrap: nowrap; justify-content: space-between;}

.container-slider .slides                               {display: flex; flex-direction: row; text-align: center; flex-wrap: nowrap; justify-content: space-evenly;}
.container-slider .slide                                {border: 1px solid salmon; padding: 10px; width: 28%}


/* Nav
/* ------------------------------------------------------------ */
nav 								                  	{display: flex; margin: 20px;}
nav li                                  {list-style: none; flex-direction: row;  flex-wrap: nowrap; justify-content: space-around;  align-items: center; margin: 0 10px; cursor: pointer; color:dodgerblue;}
nav li.active                           {color:orange; text-decoration: underline;}

